import React from 'react'
import { NavLink } from 'react-router-dom';


const Navbar = () => {
    return (
        <>
           <div className="container-fluid">
                <nav className="navbar navbar-expand-lg navbar-dark bg-primary ">
                        <div className="container-fluid">
                            <NavLink className="navbar-brand logoweb" to="/">BplDevelopers</NavLink>
                                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                                </button>
                                <div className="collapse navbar-collapse mx-auto" id="navbarSupportedContent">
                                        <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
                                            <li className="nav-item">
                                            <NavLink className="nav-link" exact to="/">Home</NavLink>
                                            </li>
                                            <li className="nav-item">
                                            <NavLink className="nav-link" exact to="/about">About</NavLink>
                                            </li>
                                            <li className="nav-item">
                                            <NavLink className="nav-link" exact to="/contact">Contact</NavLink>
                                            </li>
                                            
                                        </ul>
                                        <ul className="navbar-nav mr-5 mb-2 mb-lg-0">
                                            <li className="nav-item">
                                            <NavLink className="nav-link" to="/adduser" className="btn btn-outline-light mr-5">Add USer</NavLink>
                                            </li>
                                        </ul>
                                </div>
                        </div>
                    </nav>
           </div> 
        </>
    )
}

export default Navbar;
